.popup {
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    color: #000;
    opacity: 0;
    display: flex;
    visibility: hidden;
    transition: visibility 0.3s, opacity 0.3s;
}
